.Projects {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 93vh;
}

.Projects .container {
    width:90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Projects h1 {
    text-align: center;
    font-size: 7rem;
}

@media (max-width: 700px) {
    .Projects h1 {
        font-size: 5rem;
    }
}